.buttonRowContainer {
    display: flex;
    justify-content: center;
    padding: 0 20px; /* Add padding to limit button range */
    overflow: hidden;
  }
  
  .buttonRow {
    display: flex;
    margin-top: 20px;
    overflow: auto;
    
  }
  
  .buttonRow::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }
  
  .button {
    background: none;
    border-image-slice: 1;
    padding: 5px 10px;
    font-size: 16px;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .button:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  

.cardContainer {
    border-radius: 16px;
    margin: 70px 30px; 
  }
  
  .cardHeader {
    background-color: #ff9800; 
    color: white; 
  }
  
  .cardContent {
    display: flex;
  }
  
  .cardDescription {
    flex: 1;
    padding-right: 10px; 
  }
  
  .cardImage {
    position: relative;
    flex: 1;
  }
  
  .editImageOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Add your desired tint color */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
    cursor: pointer;
    opacity: 0.8;
  }
  